@import 'styles/index';

.base {
  position: relative;
  display: flex;

  @media (--viewport-lg) {
    display: flex;
    width: 50%;
    flex-basis: auto;
  }
}

.hasFullWidthImage {
  height: 100%;

  @media (--viewport-lg) {
    position: absolute;
    z-index: var(--z-index-background-image);
    width: 100%;
  }
}

.hasFullWidthImage.hero {
  @media (--viewport-md) {
    position: absolute;
    z-index: var(--z-index-background-image);
    width: 100%;
  }
}

.foregroundImage {
  position: absolute;
  right: 0;
  bottom: -28px;

  img {
    width: auto;
    max-width: 100%;
    min-height: 300px;
    max-height: 45vh;

    @media (--viewport-lg) {
      max-height: 55vh;
    }
  }
}

.media {
  width: 100%;
  height: 100%;

  img {
    width: 100%;
    height: 100%;
    flex: 1 0 auto;
    line-height: 0;
    object-fit: cover;
    object-position: center;
    transform: none;

    .hasFullWidthImage.hero & {
      @media (--viewport-md-only) {
        max-height: none;
      }
    }

    @media (--viewport-lg) {
      position: absolute;
      top: 0;
      right: 0;
      max-height: none;
    }
  }

  picture {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    line-height: 0;
  }
}
